___CSS_LOADER_EXPORT___.push([module.id, ".hearth:hover{color:red}", "",{"version":3,"sources":["webpack://./resources/js/vue/HomePage.vue"],"names":[],"mappings":"AA6HA,cACI,SACJ","sourcesContent":["<template>\n    <div class=\"py-2\">\n        <div v-for=\"(article, index) in Articles\">\n            <div v-if=\"index ===0\">\n                <div class=\"card mb-4\">\n                    <a href=\"#!\">\n                        <img alt=\"article.title\" @change=\"selectedFile\" class=\"card-img-top\" :src=\"article.image\"/>\n                    </a>\n                    <div class=\"card-body\">\n                        <div class=\"small text-muted\">\n                            <format class=\"text-muted\" :value=\"article.published_at\" fn=\"ago\"/>\n                        </div>\n                        <h2 class=\"card-title\">\n                            <a :href=\"article.url\">{{ article.title }}</a>\n                        </h2>\n                        <small>\n                            <a class=\"badge bg-secondary text-decoration-none link-light\"\n                               :href=\"article.category.url\">\n                                #{{ article.category.name }}\n                            </a>\n                        </small>\n\n                    </div>\n                </div>\n            </div>\n\n            <div v-else>\n\n\n                <div class=\"card mb-3\">\n                    <div class=\"row g-0\">\n                        <div class=\"col-md-4\">\n                            <img :src=\"article.image \" @change=\"selectedFile\" class=\"card-img-top h-100\"/>\n                        </div>\n                        <div class=\"col-md-8\">\n                            <div class=\"card-body\">\n                                <h3 class=\"card-title\">\n                                    <a :href=\"article.url\">{{ article.title }}</a>\n                                </h3>\n                                <p class=\"card-text\">\n                                    {{ article.excerpt }}\n                                </p>\n                                <p class=\"card-text\"><small class=\"text-muted\">\n                                    {{ article.published_at }}\n                                </small></p>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n\n            </div>\n        </div>\n        <infinite-loading @distance=\"1\" @infinite=\"handleLoadMore\"></infinite-loading>\n    </div>\n</template>\n<script>\nconsole.log('HomePage');\nexport default {\n    name: 'HomePage',\n    data() {\n        return {\n            message: this.message,\n            errored: false,\n            Articles: [],\n            Categories: [],\n            page: 1,\n            imageLoaded: false,\n        }\n    },\n    methods: {\n        handleLoadMore($state) {\n            fetch('/api/v1/articles?page=' + this.page)\n                .then(res => {\n                    return res.json();\n                }).then(res => {\n                this.Categories = res.categories;\n\n                $.each(res.result.data, (key, value) => {\n                    this.Articles.push(value);\n                });\n                $state.loaded();\n            });\n            this.page = this.page + 1;\n        },\n        showArticle(article) {\n            console.log(article)\n        },\n        imageLoadError() {\n            console.log('imageLoadError')\n            console.log('Image failed to load');\n        },\n        selectedFile() {\n            this.imageLoaded = false;\n\n            let file = this.$refs.myFile.files[0];\n            if (!file || file.type.indexOf('image/') !== 0) return;\n\n            this.image.size = file.size;\n\n            let reader = new FileReader();\n\n            reader.readAsDataURL(file);\n\n            reader.onload = evt => {\n                let img = new Image();\n                img.onload = () => {\n                    this.image.width = img.width;\n                    this.image.height = img.height;\n                    this.imageLoaded = true;\n                }\n                img.src = evt.target.result;\n            }\n\n            reader.onerror = evt => {\n                console.error(evt);\n            }\n\n        }\n    },\n    mounted() {\n        console.log(\"Welcome to the app!\");\n    }\n}\n</script>\n<style>\n.hearth:hover {\n    color: red;\n}\n</style>\n"],"sourceRoot":""}]);
